//   : React.memo 
import React, { memo } from "react";

const GiftCouponModal = memo(function GiftCouponModal({
  showGiftCouponModal,
  setShowGiftCouponModal,
  recipients,
  giftRecipient,
  setGiftRecipient, //   Dashboard  
  giftAmount,
  setGiftAmount, //   Dashboard   
  handleGiftCoupon,
  currentCoupons,
  userId,
}) {
  if (!showGiftCouponModal) {
    return null;
  }

  const validRecipients = recipients ?? [];

  const closeModal = () => {
    setShowGiftCouponModal(false);
    // ---    ---
    // setGiftAmount setGiftRecipient      
    if (typeof setGiftAmount === "function") {
      setGiftAmount(""); //     
    } else {
      console.error("setGiftAmount prop is not a function!", setGiftAmount); //   
    }
    if (typeof setGiftRecipient === "function") {
      setGiftRecipient("");
    } else {
      console.error(
        "setGiftRecipient prop is not a function!",
        setGiftRecipient
      ); //   
    }
  };

  return (
    <div
      className="modal-backdrop"
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        backgroundColor: "rgba(0, 0, 0, 0.5)",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        zIndex: 1000,
      }}
      onClick={closeModal} //    
    >
      <div
        className="gift-coupon-modal"
        style={{
          backgroundColor: "white",
          borderRadius: "12px",
          padding: "24px",
          width: "90%",
          maxWidth: "400px",
          boxShadow: "0 10px 25px rgba(0, 0, 0, 0.1)",
        }}
        onClick={(e) => e.stopPropagation()} //      
      >
        <h3 style={{ marginTop: 0, color: "#374151" }}> </h3>
        <div style={{ marginBottom: "16px", color: "#6b7280" }}>
          <p>
              : <strong>{currentCoupons}</strong>
          </p>
        </div>
        <div className="form-group" style={{ marginBottom: "16px" }}>
          <label
            style={{
              display: "block",
              marginBottom: "8px",
              fontWeight: "500",
              color: "#4b5563",
            }}
          >
             
          </label>
          <select
            value={giftRecipient}
            // onChange  setGiftRecipient  
            onChange={(e) =>
              typeof setGiftRecipient === "function"
                ? setGiftRecipient(e.target.value)
                : console.error("setGiftRecipient is not a function")
            }
            style={{
              width: "100%",
              padding: "10px",
              border: "1px solid #d1d5db",
              borderRadius: "6px",
              fontSize: "14px",
            }}
          >
            <option value="">  </option>
            {validRecipients
              .filter((recipient) => recipient.id !== userId)
              .map((recipient) => (
                <option key={recipient.id} value={recipient.id}>
                  {recipient.name} ({recipient.id})
                </option>
              ))}
          </select>
        </div>
        <div className="form-group" style={{ marginBottom: "24px" }}>
          <label
            style={{
              display: "block",
              marginBottom: "8px",
              fontWeight: "500",
              color: "#4b5563",
            }}
          >
              
          </label>
          <input
            type="number"
            value={giftAmount}
            // onChange  setGiftAmount  
            onChange={(e) =>
              typeof setGiftAmount === "function"
                ? setGiftAmount(e.target.value)
                : console.error("setGiftAmount is not a function")
            }
            placeholder="   "
            style={{
              width: "100%",
              padding: "10px",
              border: "1px solid #d1d5db",
              borderRadius: "6px",
              fontSize: "14px",
            }}
            max={currentCoupons}
            min="1"
          />
        </div>
        <div
          className="modal-actions"
          style={{ display: "flex", justifyContent: "flex-end", gap: "10px" }}
        >
          <button
            className="cancel-button"
            onClick={closeModal}
            style={{
              padding: "8px 16px",
              backgroundColor: "#f3f4f6",
              color: "#4b5563",
              border: "none",
              borderRadius: "6px",
              cursor: "pointer",
              fontWeight: "500",
            }}
          >
            
          </button>
          <button
            className="confirm-button"
            onClick={handleGiftCoupon}
            disabled={
              !giftRecipient ||
              !giftAmount ||
              parseInt(giftAmount) <= 0 ||
              parseInt(giftAmount) > currentCoupons
            }
            style={{
              padding: "8px 16px",
              backgroundColor: "#10b981",
              color: "white",
              border: "none",
              borderRadius: "6px",
              fontSize: "14px",
              fontWeight: "500",
              cursor:
                !giftRecipient ||
                !giftAmount ||
                parseInt(giftAmount) <= 0 ||
                parseInt(giftAmount) > currentCoupons
                  ? "not-allowed"
                  : "pointer",
              opacity:
                !giftRecipient ||
                !giftAmount ||
                parseInt(giftAmount) <= 0 ||
                parseInt(giftAmount) > currentCoupons
                  ? 0.7
                  : 1,
            }}
          >
            
          </button>
        </div>
      </div>
    </div>
  );
});

export default GiftCouponModal;
