// src/SellCouponModal.js
//   : React.memo 
import React, { useState, memo } from "react";

//   
const modalBackdropStyle = {
  position: "fixed",
  top: 0,
  left: 0,
  right: 0,
  bottom: 0,
  backgroundColor: "rgba(0, 0, 0, 0.5)",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  zIndex: 1000,
};

//   
const modalContainerStyle = {
  backgroundColor: "white",
  padding: "20px",
  borderRadius: "8px",
  maxWidth: "500px",
  width: "90%",
  maxHeight: "90vh",
  overflowY: "auto",
  boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
};

const SellCouponModal = memo(function SellCouponModal({
  showSellCouponModal,
  setShowSellCouponModal,
  currentCoupons = 0,
  couponValue = 1000,
  sellAmount = "",
  setSellAmount,
  // Dashboard  MyAssets     props .
  //   , Dashboard.js <SellCouponModal SellCoupon={handleSellCoupon} ... />   .
  //  prop   .
  SellCoupon, //      (Dashboard  MyAssets  )
}) {
  const [isProcessing, setIsProcessing] = useState(false);
  const [error, setError] = useState("");

  //      
  if (!showSellCouponModal) return null;

  //   
  const calculateValue = () => {
    const amount = parseInt(sellAmount);
    if (!isNaN(amount) && amount > 0) {
      return amount * couponValue;
    }
    return 0;
  };

  // handleSubmit  Dashboard handleSellCoupon (props  SellCoupon) .
  const handleSubmit = async (e) => {
    e.preventDefault();
    const amount = parseInt(sellAmount);

    //   
    setError("");

    //   
    if (!amount || isNaN(amount) || amount <= 0) {
      setError("   .");
      return;
    }

    if (amount > currentCoupons) {
      setError("      .");
      return;
    }

    // SellCoupon prop  
    if (typeof SellCoupon !== "function") {
      setError("     .");
      console.error("SellCoupon prop is not a function:", SellCoupon);
      //       .
      // alert("     .  .");
      return;
    }

    setIsProcessing(true);

    try {
      // Dashboard  MyAssets  SellCoupon  .
      // Dashboard.js handleSellCoupon   sellAmount  ,
      //         (Dashboard.js  ).
      await SellCoupon(); // Dashboard handleSellCoupon  MyAssets handleSellCoupon 

      //     Dashboard.js  MyAssets.js handleSellCoupon  
      //  , sellAmount      .
      // (: setShowSellCouponModal(false); setSellAmount("");     )
      //  Dashboard.js handleSellCoupon      sellAmount   .
    } catch (err) {
      console.error("     :", err);
      // err.message      
      setError(
        err.message || "    .  ."
      );
    } finally {
      setIsProcessing(false);
      // /   isProcessing  false 
      //         SellCoupon  
    }
  };

  const handleCancel = () => {
    setSellAmount("");
    setError("");
    setShowSellCouponModal(false);
  };

  //    
  const isInvalidAmount =
    !sellAmount ||
    isNaN(parseInt(sellAmount)) ||
    parseInt(sellAmount) <= 0 ||
    parseInt(sellAmount) > currentCoupons;

  return (
    <div style={modalBackdropStyle} onClick={handleCancel}>
      <div style={modalContainerStyle} onClick={(e) => e.stopPropagation()}>
        <h3
          style={{
            fontSize: "18px",
            fontWeight: "600",
            marginBottom: "15px",
            color: "#1f2937",
          }}
        >
           
        </h3>
        <p
          style={{
            marginBottom: "10px",
            fontSize: "14px",
            color: "#4b5563",
          }}
        >
             .
        </p>
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            marginBottom: "15px",
            padding: "10px",
            backgroundColor: "#f9fafb",
            borderRadius: "6px",
            fontSize: "15px",
          }}
        >
          <span>  :</span>
          <strong>{currentCoupons.toLocaleString()}</strong>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            marginBottom: "15px",
            padding: "10px",
            backgroundColor: "#f9fafb",
            borderRadius: "6px",
            fontSize: "15px",
            color: "#4f46e5",
          }}
        >
          <span>1 :</span>
          <strong>{couponValue.toLocaleString()}</strong>
        </div>

        <form onSubmit={handleSubmit}>
          <div style={{ marginBottom: "15px" }}>
            <label
              htmlFor="sellAmount"
              style={{
                display: "block",
                marginBottom: "5px",
                fontWeight: "500",
                color: "#374151",
              }}
            >
                :
            </label>
            <input
              id="sellAmount"
              type="number"
              value={sellAmount}
              onChange={(e) => setSellAmount(e.target.value)}
              min="1"
              max={currentCoupons} //  
              style={{
                width: "100%",
                padding: "8px 12px",
                border: "1px solid #d1d5db",
                borderRadius: "6px",
                fontSize: "14px",
                outline: "none", //     
              }}
              disabled={isProcessing}
              required // HTML5   
            />
          </div>

          {/*    */}
          {error && (
            <div
              style={{
                backgroundColor: "#fee2e2", //  
                border: "1px solid #fecaca", //  
                borderRadius: "6px",
                padding: "12px 15px",
                marginBottom: "15px",
                color: "#b91c1c", //   
                fontSize: "14px",
              }}
            >
              <p style={{ margin: 0 }}> {error}</p>
            </div>
          )}

          <div
            style={{
              marginBottom: "20px",
              padding: "10px 15px",
              backgroundColor: "#eef2ff", //   
              borderRadius: "6px",
              borderLeft: "4px solid #4f46e5", //   
            }}
          >
            <div
              style={{
                display: "flex",
                justifyContent: "space-between",
                fontSize: "16px",
                fontWeight: "600",
                color: "#1f2937", //   
              }}
            >
              <span> :</span>
              <span style={{ color: "#4f46e5" /*   */ }}>
                {calculateValue().toLocaleString()}
              </span>
            </div>
          </div>

          <div
            style={{
              display: "flex",
              justifyContent: "flex-end",
              gap: "10px",
              marginTop: "20px",
            }}
          >
            <button
              type="button"
              onClick={handleCancel}
              style={{
                padding: "8px 16px",
                backgroundColor: "#f3f4f6", //   
                color: "#4b5563", //   
                border: "none",
                borderRadius: "6px",
                fontSize: "14px",
                cursor: "pointer",
                transition: "background-color 0.2s ease",
              }}
              disabled={isProcessing}
            >
              
            </button>
            <button
              type="submit"
              style={{
                padding: "8px 16px",
                backgroundColor: "#ef4444", //   ( )
                color: "white", //  
                border: "none",
                borderRadius: "6px",
                fontSize: "14px",
                fontWeight: "500",
                cursor:
                  isInvalidAmount || isProcessing ? "not-allowed" : "pointer", //   
                opacity: isInvalidAmount || isProcessing ? 0.6 : 1, //  
                transition: "background-color 0.2s ease, opacity 0.2s ease",
              }}
              disabled={isInvalidAmount || isProcessing} //  
            >
              {isProcessing ? " ..." : ""}
            </button>
          </div>
        </form>
      </div>
    </div>
  );
});

export default SellCouponModal;
